import { ReefFile, ReefFolderV2, ReefRepositoryV2, ReefFileEntry } from "./ReefRepository";
import { AbiItem } from 'web3-utils'
import Web3 from 'web3';
import { Contract } from "web3-eth-contract"
import contractABI from './abiV2.json'
import axios from 'axios';

export class ReefRepositoryV2Implement implements ReefRepositoryV2 {
	contractAddr = "0xa1BCe629767D23cb719990FB8aDF54a2B4Da41d1"
    web3: Web3;
    contract: Contract;

    base = "https://quorum-blockchain.yikuo.dev/api/";
    rpc = "https://rpc.quorum-blockchain.yikuo.dev/"

    constructor() {
        this.web3 = new Web3(this.rpc);
        this.contract = new this.web3.eth.Contract( contractABI as AbiItem[], this.contractAddr);
    }
	
	async listFolders(folderIdx: number): Promise<ReefFolderV2[]> {
		let arrayLength = await this.contract.methods.getFolderListLength(folderIdx).call();
		let promiseArray = [];
        for (let i = 0; i < arrayLength ; i++) {
            promiseArray.push(this.contract.methods.getFolderList(folderIdx, i).call());
        }
        let IdxArray = await Promise.all(promiseArray);
		promiseArray = [];
		for (let i = 0; i < IdxArray.length; i ++){
			promiseArray.push(this.contract.methods.getFolderName(IdxArray[i]).call())
		}
		let NameArray = await Promise.all(promiseArray);
		let Result: ReefFolderV2[] = []
		for (let i = 0; i < IdxArray.length; i++){
			let tmp = {
				folderIdx: IdxArray[i],
				name: NameArray[i],
			}
			Result.push(tmp);
		}
		return Result;
	}
	async listFolderFiles(folderIdx: number): Promise<ReefFileEntry[]>{
		let arrayLength = await this.contract.methods.getFolderExcelLength(folderIdx).call();
		let promiseArray = [];
        for (let i = 0; i < arrayLength ; i++) {
            promiseArray.push(this.contract.methods.getFolderExcel(folderIdx, i).call());
        }
        let excelArray = await Promise.all(promiseArray);
		let Result: ReefFileEntry[] = []
		for (let i = 0; i < excelArray.length; i++){
			let tmp = {
				folderIdx: folderIdx,
				name: excelArray[i],
			}
			Result.push(tmp);
		}
		return Result;
	}
	async listFiles(entry: ReefFileEntry): Promise<ReefFile[]>{
        
        let arrayLength = await this.contract.methods.getExcelLength(entry.folderIdx, entry.name).call()
        console.log("TEST:", arrayLength)
        let promiseArray = [];
        for (let i = 0 ; i < arrayLength ; i++) {
            promiseArray.push(this.contract.methods.getExcel(entry.folderIdx, entry.name, i).call());
        }
        let excelArray = await Promise.all(promiseArray);
		let Result: ReefFile[] = [];
        for (let i = 0 ; i < excelArray.length ; i++){
            let tmp = {
                uuid: excelArray[i]["cid"],
                name: entry.name,
                size: +excelArray[i]["size"],
                lastModified: new Date(+excelArray[i]["time"] * 1000)
            }
            Result.push(tmp);
        }
        return Result;
	}
	async downloadFile(fileUUID: string, fileName: string): Promise<void>{
        axios({
            url: this.base + `data/${fileUUID}/download` ,
            method: 'GET',
            responseType: 'blob'
        })
            .then((response) => {
                const url = window.URL
                    .createObjectURL(new Blob([response.data]));
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', fileName);
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            })
	}
}