 import { Alert, AlertColor, Snackbar } from '@mui/material';
import {
    ChonkyActions,
    ChonkyFileActionData,
    FileArray,
    FileBrowser,
    FileContextMenu,
    FileList,
    FileNavbar,
    FileToolbar,
    setChonkyDefaults,
} from 'chonky';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { ReefFile, ReefFileEntry, ReefFolderV2, ReefRepositoryV2 } from '../data/ReefRepository';

setChonkyDefaults({ iconComponent: ChonkyIconFA });

interface Path {
    index: number,
    name: string
}

interface Folder {
    path: Path[],
    index: number
}

interface SnackBarInfo {
    message: string,
    severity: AlertColor
}

export const useFolderChain = (folder: Folder, file: ReefFileEntry | null): FileArray => {
    return useMemo(() => {
        const folderChain: FileArray = folder.path.map((p: Path) => {
            return {
                id: p.index.toString(),
                name: p.name
            };
        });

        if (file != null) {
            folderChain.push({
                id: `${file.folderIdx}/${file.name}`,
                name: file.name + " (History)"
            })
        }

        console.log(folderChain);
        return folderChain;
    }, [folder, file]);
};

export const useFileActionHandler = (
    reefRepository: ReefRepositoryV2,
    isInFile: boolean,
    folder: Folder,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
    setFolder: (folder: Folder) => void,
    setFile: (file: ReefFileEntry | null) => void
) => {
    return useCallback(
        (data: ChonkyFileActionData) => {
            if (data.id === ChonkyActions.OpenFiles.id) {
                const { targetFile, files } = data.payload;
                const fileToOpen = targetFile ?? files[0];
                if (fileToOpen) {
                    if (fileToOpen.isDir) {
                        setFolder({
                            path: [...folder.path, {
                                index: parseInt(fileToOpen.id),
                                name: fileToOpen.name
                            }],
                            index: parseInt(fileToOpen.id)
                        })
                        setFile(null);
                    } else {
                        if (isInFile) {
                            setSnackbarInfo({
                                message: "The file is now downloading...",
                                severity: 'info'
                            });

                            reefRepository.downloadFile(fileToOpen.id, fileToOpen.name)
                        } else {
                            const [idx, name] = fileToOpen.id.split("/", 1);

                            setFile({
                                folderIdx: parseInt(idx),
                                name: name
                            });
                        }
                    }
                }
            }

            console.log(data);
        },
        [setFolder, folder.path, setFile, isInFile, setSnackbarInfo, reefRepository]
    );
};

export const updateFolderFiles = (
    reefRepository: ReefRepositoryV2,
    folder: Folder,
    setFolderFiles: (folderFiles: FileArray) => void,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
) => {
    setFolderFiles([null]);

    const subFolders = reefRepository.listFolders(folder.index);
    const subFiles = reefRepository.listFolderFiles(folder.index);

    Promise.all([subFolders, subFiles]).then((res) => {
            const subFolders = res[0];
            const subFiles= res[1];

            const folders: FileArray = subFolders.map((subFolder: ReefFolderV2) => {
                return {
                    id: subFolder.folderIdx.toString(),
                    name: subFolder.name,
                    isDir: true
                };
            });

            const files: FileArray = subFiles.map((subFile: ReefFileEntry) => {
                return {
                    id: `${subFile.folderIdx}/${subFile.name}`,
                    name: subFile.name,
                    isDir: false
                };
            });

            console.log(folders.concat(files));
            setFolderFiles(folders.concat(files));
        })
        .catch((err: any) => {
            console.log(err);
            setSnackbarInfo({
                message: `${err}`,
                severity: 'error'
            });
        });
}

export const updateVersionFiles = (
    reefRepository: ReefRepositoryV2,
    file: ReefFileEntry | null,
    setVersionFiles: (versionFiles: FileArray | null) => void,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
) => {
    if (file == null) {
        setVersionFiles(null);
    } else {
        setVersionFiles([null]);
        reefRepository.listFiles(file)
            .then((reefFiles: ReefFile[]) => {
                const versionFiles: FileArray = reefFiles.map((reefFile: ReefFile) => {
                    return {
                        id: reefFile.uuid,
                        name: reefFile.name,
                        modDate: reefFile.lastModified,
                        size: reefFile.size,
                        isDir: false
                    }
                })

                setVersionFiles(versionFiles);
            })
            .catch((err: any) => {
                console.log(err);
                setSnackbarInfo({
                    message: `${err}`,
                    severity: 'error'
                });
            });
    }
}


export const ReefBrowser: React.FC<{ instanceId: string, reefRepository: ReefRepositoryV2 }> = (props) => {
    const [snackbarInfo, setSnackbarInfo] = useState(null as (SnackBarInfo | null));
    
    const [folder, setFolder] = useState({
        path: [{
            index: 0,
            name: 'Root'
        }],
        index: 0
    } as Folder);
    const [file, setFile] = useState(null as (ReefFileEntry | null));

    const [folderFiles, setFolderFiles] = useState([null] as FileArray);
    useEffect(() => {
        updateFolderFiles(props.reefRepository, folder, setFolderFiles, setSnackbarInfo);
    }, [folder, props.reefRepository])

    const [versionFiles, setVersionFiles] = useState(null as (FileArray | null));
    useEffect(() => {
        updateVersionFiles(props.reefRepository, file, setVersionFiles, setSnackbarInfo);
    }, [file, props.reefRepository])

    const folderChain = useFolderChain(folder, file);
    const handleFileAction = useFileActionHandler(
        props.reefRepository,
        file != null,
        folder,
        setSnackbarInfo,
        setFolder,
        setFile
    );
    return (
        <div style={{ height: '80vh' }}>
            <FileBrowser
                instanceId={props.instanceId}
                files={versionFiles ?? folderFiles}
                folderChain={folderChain}
                onFileAction={handleFileAction}
                defaultFileViewActionId={ChonkyActions.EnableListView.id}
            >
                <FileNavbar />
                <FileToolbar />
                <FileList />
                <FileContextMenu />
            </FileBrowser>

            <Snackbar
                open={snackbarInfo !== null}
                autoHideDuration={3000}
                onClose={() => { setSnackbarInfo(null) }}
                ClickAwayListenerProps={{onClickAway: () => {}}}
            >
                <Alert
                    onClose={() => { setSnackbarInfo(null) }}
                    severity={snackbarInfo?.severity}
                    sx={{ width: '100%' }}
                >
                    {snackbarInfo?.message}
                </Alert>
            </Snackbar>
        </div>
    );
};